/* 使用本地作用域類名，確保樣式僅在 Game1 中應用 */
:local(.container1) {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    background: #000;
    color: #fff;
    text-align: center;
}

:local(.button-container) {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000; // 確保按鈕在最上層
}

:local(.end-button) {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;

    &:hover {
        background-color: #45a049;
    }
}

/* 使用媒體查詢來適應不同的螢幕大小 */
@media (max-width: 768px) {
    :local(.end-button) {
        font-size: 14px;
        padding: 8px 16px;
    }
}

@media (max-width: 480px) {
    :local(.end-button) {
        font-size: 12px;
        padding: 6px 12px;
    }
}

/* 隐藏背景样式 */
body.hide-background {
    background-image: none !important;
}