.container4{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100vh;width:100vw;background-size:cover;background-position:center;padding:20px;background-image:url(../../assets/picture/BG/背景-草地.png)}.mapContainer{display:flex;align-items:center;position:relative;margin-bottom:20px}.mapImageContainer{position:relative;width:100%;max-width:600px}.mapImage{width:100%;height:auto}.map-buttons-container{display:flex;flex-direction:column;position:absolute;right:10px;top:50%;transform:translateY(-50%)}.map-toggle-button,.input-toggle-button{padding:10px;font-size:16px;background-color:#4caf50;color:#fff;border:none;border-radius:5px;cursor:pointer;display:flex;align-items:center;justify-content:center;transition:background-color .3s ease;margin-bottom:10px}.map-toggle-button:hover,.input-toggle-button:hover{background-color:#45a049}.map-toggle-button svg,.input-toggle-button svg{font-size:18px}.input-toggle-button{margin-bottom:0}.audioButtonsContainer{display:flex;flex-direction:column;align-items:flex-start;margin-right:10px}.audio-button{padding:10px;font-size:16px;background-color:#4caf50;color:#fff;border:none;border-radius:5px;cursor:pointer;margin-bottom:10px;display:flex;align-items:center;justify-content:center;transition:background-color .3s ease}.audio-button:hover{background-color:#45a049}.audio-button svg{font-size:24px}.resultContainer{display:flex;align-items:center;justify-content:center;position:absolute;bottom:0;left:50%;transform:translateX(-50%);background:rgba(255,255,255,.8);padding:10px;border-radius:0 0 10px 10px;width:100%;max-width:600px}.input{padding:10px;font-size:16px;border:1px solid #ccc;border-radius:5px;flex-grow:1;margin-right:10px}.end-button{padding:10px 20px;font-size:16px;background-color:#4caf50;color:#fff;border:none;border-radius:5px;cursor:pointer;white-space:nowrap;transition:background-color .3s ease}.end-button:hover{background-color:#45a049}.newPrompt{margin-top:20px;font-size:18px;color:#333}.navigate-button{padding:10px 20px;font-size:16px;background-color:#4caf50;color:#fff;border:none;border-radius:5px;cursor:pointer;margin-top:10px;transition:background-color .3s ease}.navigate-button:hover{background-color:#45a049}@media(max-width: 768px){.mapImage{width:70%}.map-toggle-button,.input-toggle-button,.audio-button{padding:8px 16px;font-size:14px}.input{width:150px}}@media(max-width: 480px){.mapImage{width:100%}.map-toggle-button,.input-toggle-button,.audio-button{padding:6px 12px;font-size:12px}.input{width:100px}}